body,
button,
input,
select,
optgroup,
textarea {
	color: $color__text-main;
	font-family: $font__serif;
	@include font-size(.9);
	line-height: $font__line-height-body;
        line-height: 1.6;
        font-weight: 400;
  
        
        @media screen and (min-width: 700px) {
                @include font-size(1);

        }

}

@import "headings";

@import "copy";